.card img {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  right: 0;
  object-fit: cover;
  background-color: $color-tangaroa;
}

.card {
  display: flex;
  flex-direction: column-reverse;
  position: relative;
  padding: 28px 25px 36px;
  min-height: 386px;
  background-color: $color-tangaroa;
  color: $color-alice-blue;
  overflow: hidden;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(1, 28, 64, 0) 50%, #011C40 100%);
  }

  &:hover,
  &:focus-within {
    flex-direction: column;
    background-color: $color-tangaroa;

    img {
      z-index: -1;
    }

    h3 {
      position: static;
    }

    &::after {
      display: none;
    }
  }

  @media (max-width: $vp-1023) {
    padding: 30px 35px 36px;
  }

  @media (max-width: $vp-767) {
    padding: 29px 25px 36px;
  }
}

.card h3 {
  margin-top: 0;
  margin-bottom: 13px;
  font-weight: 500;
  font-size: 18px;
  z-index: 1;
  position: relative;
  left: 5px;
  bottom: -27px;

  @media (max-width: $vp-1023) {
    left: 0;
    bottom: -22px;
  }

  @media (max-width: $vp-767) {
    bottom: -32px;
  }
}

.card dl {
  margin: 0;
}

.card dt {
  margin-bottom: 0;
  font-weight: 500;
  font-size: 14px;
}

.card dd {
  margin-left: 0;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 14px;
}

.card a {
  margin-top: auto;
  padding: 10px;
  background-color: $color-alice-blue;
  color: $color-tangaroa;
  font-weight: 600;
  font-size: 14px;
  justify-content: center;

  @media (max-width: $vp-1023) {
    padding: 8px;
  }
}

.card .card__price {
  margin-bottom: 1px;
}
